@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Newsreader:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Playfair+Display:wght@500&family=Rokkitt:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Newsreader:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Playfair+Display:wght@500&family=Rokkitt:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ArticleArea
{
    object-fit: contain;
}

.Article
{
    padding: 10px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.938);
    margin-bottom: 25px;
    border-radius: 10px;
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(206, 206, 206, 0.116);
}

.ImageOfArticle
{
    max-width: 90%;
    max-height: 90%;
    display:block;
    margin-left:auto;
    margin-right: auto;
    object-fit: contain;
    border-radius: 2%;
    margin-bottom: 27px;
}

.TextOfArticleArea
{
    text-align: center;
    padding-left: 17px;
    padding-right: 17px;
}

.TitleOfArticleArea
{
    object-fit: contain;
    margin-bottom: 4px;
}

.TitleOfArticleFont
{
    font-size: 158%;
    font-weight: bold;
    font-family: 'STIX Two Text';
    line-height: 25px;
}

.AuthorOfArticleArea
{
    object-fit: contain;
    margin-bottom: 22px;
}

.AuthorOfArticleFont
{
    font-family: 'serif';
    font-size: 75%;
    line-height: 0px;
}

.ContentOfArticleArea
{
    text-align: left;
    overflow: hidden;
    margin-bottom: 30px;
}

.ContentOfArticleFont
{
    white-space: pre-wrap;
    font-family: 'Newsreader';
    font-weight: bold;
    font-size: 90%;
}

.ShareButtonsList
{
    text-align:center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.ShareElement
{
    display:inline;
}
.nav
{
    height: 76px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.938);
    margin-bottom: 16px;
    display: flex;
    align-items: center;
}

.icon
{
    flex: .5 1;
}

.image
{
    height: auto;
    width: 150px;
}
.NewsCardArea
{
    object-fit: contain;
}

.NewsCard
{
    display: flex;
    padding: 10px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.938);
    margin-bottom: 25px;
    border-radius: 10px;
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(206, 206, 206, 0.116);
}

.ImageOfNews
{
    object-fit: cover;
    width: 36%;
    height: 36%;
    border-radius: 7%;
}

.TextOfNewsArea
{
    padding-left: 13px;
    padding-right: 13px;
    object-fit: contain;
    margin-bottom: 1px;
}

.TitleOfNewsFont
{
    font-size: 158%;
    font-weight: bold;
    font-family: 'STIX Two Text';
    line-height: 16px;
}

.AuthorOfNewsArea
{
    object-fit: contain;
    margin-bottom: 8px;
}

.AuthorOfNewsFont
{
    font-family: 'serif';
    font-size: 75%;
    line-height: 0px;
}

.DescriptionOfNewsFont
{
    font-family: 'Newsreader';
    font-weight: bold;
    font-size: 90%;
}

.DescriptionOfNewsArea
{
    margin-bottom: 16px;
}

.ReadMoreArea
{
  display: flex;
  object-fit: contain;
  margin-bottom: 10px;
}

.ReadMore
{
  font-family: "Playfair Display";
  font-weight: bolder;
  font-size: 90%;
}

.moreButton
{
    width: 15%;
    left: 44%;
}
